.ImageGallery {
  display: grid;
  max-width: calc(100vw - 24px);
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 1200px) {
  .ImageGallery {
    max-width: calc(100vw - 48px);
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  }
}
